input{
    padding: 10px;
    border: none;
    outline: 1px rgba(238, 238, 238, 0.653) solid;
    border-radius: 20px;
    font-size: 18px;
    text-align: center;
    display: grid;
    justify-content: center;
    margin: auto;
    background-color: transparent;
}

h1{
    margin-top: 30px;
    text-align: center; 
    font-weight: bold;
}

h2{
    text-align: center; 
    font-weight: bold;
}

h4{
    font-size: 14px;
    text-align: center;
    margin-top: 20px;
}

p{
    margin-top: 80px;
    text-align: center;
}